import React from 'react';
import {useSelector} from "react-redux";
import ProductCard from "../../template/ProductCard";
import BodyTemplate from "../../template/BodyTemplate";
import LinkTemplate from "../../template/LinkTemplate";
import CounterContainer from "../../template/Counter/CounterContainer";
import {SlideTemplate} from "../../template/slide-template/SlideTemplate";

const HotProduct = () => {
    const hotProducts = useSelector(state => state.prefetchDataReducer.hot_product)
    const listProduct = hotProducts.map((item, index) => {
        return <div key={index} className='home-product-sale-element'>
            <ProductCard data={item}/>
        </div>
    })
    return (
        <div className='home-product-sale-container'>
            <div className='home-product-sale-title'>
                <LinkTemplate path={'/khuyen-mai'}>
                               <span className='home-product-sale-text-title'>
                                   Khuyến mãi đặc biệt
                               </span>
                </LinkTemplate>
                <div style={{flexGrow: 1}}></div>
                {/*<div className='home-product-sale-counter'>*/}
                {/*    <CounterContainer end_date={'2020-07-15T00:00:00'}/>*/}
                {/*</div>*/}
            </div>
            <div className='home-product-sale-body'>
                <SlideTemplate data={listProduct} isFixed={false} numDesktop={5} numMedium={4}
                               numMobile={2}
                               isInterval={true}/>
            </div>
        </div>
    );
};

export default HotProduct;