import React, {useEffect, useState} from 'react';
import './HomeNews.css'
import LinkTemplate from "../../template/LinkTemplate";
import NewsCard from "../../template/NewComponents/NewsCard";
import {useHistory} from 'react-router-dom'
import {getItems} from "../../../api/api";
import {SlideTemplate} from "../../template/slide-template/SlideTemplate";

const HomeNews = () => {
    const [state, setState] = useState({
        list: []
    })
    const history = useHistory()
    useEffect(() => {
        getItems('/news?page=1&item_per_page=3&type_news=0', {timeout: 20000})
            .then(res => setState({
                ...state,
                list: res.data.list
            }))
    }, [])
    const list_news = state.list.map((item, index) => {
        return <div style={{padding : '0.5em'}}>
            <NewsCard data={item}/>
        </div>
    })
    return (
        <React.Fragment>
            <section className='home-news-title'>
                <LinkTemplate path={`/tin-tuc`}>
                    <div className='home-news-title-text'
                         style={{
                             backgroundColor: 'var(--main-bg-color)'
                         }}
                    >
                        TIN TỨC
                    </div>
                </LinkTemplate>
            </section>
            {/*<section className='home-news-container'>*/}
            {/*    {list_news}*/}
            {/*</section>*/}
            <section className='home-news-container-mobile'>
                <SlideTemplate data={list_news} numDesktop={3} numMedium={2} numMobile={1}/>
            </section>
            <section className='home-news-button-container'>
                <button className='home-news-button' onClick={() => {
                    history.push('/tin-tuc')
                }}>Xem thêm
                </button>
            </section>
        </React.Fragment>
    );
};

export default HomeNews;