import React, {useEffect, useState} from "react";
import "./Home.css";
import Slide from "./components/Slide";
import {connect, useSelector} from "react-redux";
import Navigation from "../../MainComponent/header/navigation/Navigation";
import {useDispatch} from "react-redux";
import {
    allowTitleHover,
    disAllowTitleHover,
} from "../../redux/actions/actions";
import {SlideTemplate} from "../template/slide-template/SlideTemplate";
import LinkTemplate from "../template/LinkTemplate";
import BodyTemplate from "../template/BodyTemplate";
import {getItems} from "../../api/api";
import HomeCategory from "./components/HomeCategory";
import HotProduct from "./components/HotProduct";
import HomeNews from "./components/HomeNews";
import BannerRect from "../../assets/banner/Banner-rectangle.jpg"
import BannerRect2 from "../../assets/banner/banner-rectangle-2.jpg"
import BannerMiddle1 from "../../assets/banner/Banner-middle.jpg"
import BannerMiddle2 from "../../assets/banner/Banner-middle-2.jpg"
import {useHistory} from 'react-router-dom'
import HomeSkeleton from "../skeleton/HomeSkeleton";
import MetaTag from "../template/MetaTag/MetaTag";

function Home() {
    const dispatch = useDispatch();
    const navigation = useSelector(
        (state) => state.prefetchDataReducer.navigation
    );
    const [banner, setBanner] = useState([])
    const [listCat, setListCat] = useState({
        isLoading: true,
        list: []
    })
    const history = useHistory()
    useEffect(() => {
        dispatch(disAllowTitleHover());
        getItems(`/banner`, {timeout: 20000})
            .then(res => {
                const list_banner = res.data.list.map(item => {
                    return {
                        url: item.image_url,
                        path: item.name,
                    }
                })
                setBanner(list_banner)
            })
            .catch(error => console.log(error))
        getItems(`/category/detail`, {timeout: 20000})
            .then(res => {
                setListCat({
                    ...listCat,
                    isLoading: false,
                    list: res.data.list
                })
            })
            .catch(error => console.log(error))
        return () => {
            dispatch(allowTitleHover());
        };
    }, []);
    const listNavScroll = navigation.map((item, index) => {
        return (
            <div className="home-slider-nav-element" key={index}>
                <LinkTemplate path={`/p/${item.path}`}>
                    <div className="home-slider-nav-wrapper">
                        <div className="home-slider-nav-icon-container" style={{backgroundImage: `url(${item.icon})`}}>
                            <div className='home-slider-inner-text'>
                            </div>
                        </div>
                        <div className={"home-slider-nav-name"}>{item.name}</div>
                    </div>
                </LinkTemplate>
            </div>
        );
    });
    return (
        <React.Fragment>
            <MetaTag title={'Điên máy VIỆT NHẬT - Chuyên cung cấp máy thực phẩm số 1 Việt Nam'}/>
            {
                listCat.isLoading ? <HomeSkeleton/> :
                    <div className="home-container">
                        <BodyTemplate>
                            <section className="home-header-wrapper">
                                <div className="home-navigation">
                                    <Navigation/>
                                </div>
                                <div className='home-slide-container'>
                                    <Slide data={banner} aspect_ratio={2.5} timeout={10000}/>
                                </div>
                                <div className="home-side">
                                    <div
                                        className="home-side-wrapper side-1"
                                        style={{backgroundImage: `url(${BannerRect})`}}
                                        onClick={() => {
                                            history.push('/khuyen-mai')
                                        }}
                                    >
                                        <div className="home-side-ripple"></div>
                                    </div>
                                    <div
                                        className="home-side-wrapper side-2"
                                        style={{backgroundImage: `url(${BannerRect2})`}}
                                        onClick={() => {
                                            history.push('/khuyen-mai')
                                        }}
                                    >
                                        <div className="home-side-ripple"></div>
                                    </div>
                                </div>
                            </section>
                            <section className='home-slider-nav'>
                                {/*<article className='home-slider-nav-title'>*/}
                                {/*    DANH MỤC SẢN PHẨM*/}
                                {/*</article>*/}
                                <div className='home-slider-body'>
                                    <SlideTemplate data={listNavScroll} isFixed={false} numDesktop={6} numMedium={4}
                                                   numMobile={2}
                                                   isInterval={true}/>
                                </div>
                            </section>
                        </BodyTemplate>
                        <BodyTemplate>
                            <section className='home-banner-image-container'>
                                <LinkTemplate path={'/gioi-thieu/gioi-thieu-ve-cong-ty'}
                                              className={'home-banner-image'}>
                                    <img style={{width: '100%'}}
                                         src={BannerMiddle1}/>
                                </LinkTemplate>
                                <LinkTemplate path={'/ho-tro'} className={'home-banner-image'}>
                                    <img style={{width: '100%'}}
                                         src={BannerMiddle2}/>
                                </LinkTemplate>
                            </section>
                        </BodyTemplate>
                        <section className='home-product-sale'>
                            <BodyTemplate>
                                <HotProduct/>
                            </BodyTemplate>
                        </section>
                        <section>
                            <BodyTemplate>
                                {
                                    listCat.list.map((item, index) => {
                                            if (index > 1 && index != 5) {
                                                return null
                                            }
                                            return <div key={index} style={{marginTop: '2em'}}>
                                                <HomeCategory category={item} index={index}/>
                                            </div>
                                        }
                                    )
                                }
                            </BodyTemplate>
                        </section>

                        <section>
                            <BodyTemplate>
                                <HomeNews/>
                            </BodyTemplate>
                        </section>
                    </div>
            }
        </React.Fragment>
    );
}

const mapStateToProps = (state) => ({
    data: state.categoryDataReducer,
});
export default connect(mapStateToProps)(Home);
