import React from 'react';
import BodyTemplate from "../template/BodyTemplate";
import SkeletonTemplate from "../template/Skeleton/SkeletonTemplate";
import './HomeSkeleton.css'
import LinkTemplate from "../template/LinkTemplate";
import BannerMiddle1 from "../../assets/banner/Banner-middle.jpg";
import BannerMiddle2 from "../../assets/banner/Banner-middle-2.jpg";

const HomeSkeleton = () => {
    return (
        <div className="home-container">
            <BodyTemplate>
                <section className="home-header-wrapper">
                    <div className="home-navigation">
                        <SkeletonTemplate
                            animation={true}
                            width="100%"
                            height="18em"
                            extraStyle={{}}
                        />
                    </div>
                    <div className="home-slide-container">
                        <div className="home-skeleton-slide-container">
                            <div className="home-skeleton-slide-element">
                                <SkeletonTemplate animation={true} width="100%" height="100%"/>
                            </div>
                        </div>
                    </div>
                    <div className="home-side">
                        <SkeletonTemplate animation={true} width="100%" height="49%"/>
                        <SkeletonTemplate animation={true} width="100%" height="49%"/>
                    </div>
                </section>
                <section className="home-slider-nav">
                    {/*<article className='home-slider-nav-title'>*/}
                    {/*    DANH MỤC SẢN PHẨM*/}
                    {/*</article>*/}
                    <SkeletonTemplate animation={true} width={`100%`} height="7em"/>
                </section>
            </BodyTemplate>
            <BodyTemplate>
                <section className='home-banner-image-container'>
                    <div className='home-skeleton-middle-container'>
                        <div className='home-skeleton-middle-element'>
                            <SkeletonTemplate height={'100%'} width={'100%'} animation={true}/>
                        </div>
                    </div>
                    <div className='home-skeleton-middle-container'>
                        <div className='home-skeleton-middle-element'>
                            <SkeletonTemplate height={'100%'} width={'100%'} animation={true}/>
                        </div>
                    </div>

                </section>
            </BodyTemplate>
        </div>
    );
};

export default HomeSkeleton;