import React from 'react';
import './Homecategory.css'
import ProductCard from "../../template/ProductCard";
import {SlideTemplate} from "../../template/slide-template/SlideTemplate";
import LinkTemplate from "../../template/LinkTemplate";
import BannerChao from "../../../assets/banner/Banner-vertical.jpg"

const HomeCategory = ({category, index}) => {
    const list_product = category.list_product.map((item, index) => {
        return <div key={index} className='home-category-element'>
            <ProductCard data={item}/>
        </div>
    })
    return (
        <React.Fragment>
            {
                category.list_product.length !== 0 ?
                    <React.Fragment>
                        <div className='home-category-title'>
                            <LinkTemplate path={`/p/${category.path}`}>
                                <div className='home-category-title-text'
                                     style={{
                                         backgroundColor: index === 0 ? 'var(--main-bg-color)' :
                                             index === 1 ? 'var(--main-bg-color)' : 'var(--main-bg-color)'
                                     }}
                                >
                                    {category.name.toUpperCase()}
                                </div>
                            </LinkTemplate>
                            <div style={{flexGrow: 1}}></div>
                            {
                                category.child_category.map((item, index) => {
                                    return <LinkTemplate path={`/c/${item.path}`} key={index}>
                                        <div className='home-category-child'>
                                            {item.name}
                                        </div>
                                    </LinkTemplate>
                                })
                            }
                        </div>
                        <div className='home-category-body-product-desktop'>
                            {
                                index === 0 || index === 5 ?
                                    <div>
                                        <LinkTemplate path={`/p/${category.path}`}>
                                            <div className='home-category-banner'
                                                 style={{backgroundImage: `url(${category.banner})`}}>
                                                <div className='home-category-inner-text'></div>
                                            </div>
                                        </LinkTemplate>
                                        <div style={{paddingTop: '1em'}}>
                                            <SlideTemplate data={list_product} numMobile={2} numMedium={3}
                                                           numDesktop={5}/>
                                        </div>
                                    </div> :
                                    <div className='home-category-product-container'
                                        //  style={{
                                        // borderTop: index === 0 ? '2px solid var(--main-bg-color)' :
                                        //     index === 1 ? '2px solid var(--second-bg-color)' : '2px solid var(--third-bg-color)'
                                        // }}
                                    >
                                        <div className='home-category-product-element'>
                                            <div className='home-category-banner-left-container'>
                                                <LinkTemplate path={`/p/${category.path}`}
                                                >
                                                    <div
                                                        style={{backgroundImage: `url(${index === 1 ? BannerChao : 'https://bizweb.dktcdn.net/100/374/880/themes/748270/assets/banner_doc_1.jpg?1591756297405'})`}}
                                                        className='home-category-banner-left'>
                                                        <div className={'home-category-text-inner'}>

                                                        </div>
                                                    </div>
                                                </LinkTemplate>
                                            </div>
                                            {list_product}
                                        </div>
                                    </div>
                            }
                        </div>
                        <div className='home-category-mobile'
                            //      style={{
                            //     borderTop: index === 0 ? '2px solid var(--main-bg-color)' :
                            //         index === 1 ? '2px solid var(--second-bg-color)' : '2px solid var(--third-bg-color)'
                            // }}
                        >
                            <LinkTemplate path={`/p/${category.path}`}>
                                <div className='home-category-banner'
                                     style={{backgroundImage: `url(${category.banner})`}}>
                                    <div className='home-category-inner-text'></div>
                                </div>
                            </LinkTemplate>
                            <div style={{paddingTop: '0.5em'}}>
                                <SlideTemplate data={list_product} numMobile={2.5} numMedium={3}
                                               numDesktop={5}/>
                            </div>
                        </div>
                    </React.Fragment> : null
            }
        </React.Fragment>
    );
};

export default HomeCategory;