import React from 'react';
import LinkTemplate from "../LinkTemplate";
import './NewsCard.css'
const NewsCard = ({data}) => {
    return (
        <div style={{display: 'flex', flexDirection:"column"}}>
            <div className='news-card-image'
                 style={{backgroundImage: `url(${data.image})`}}>
                <div className='news-card-inner-text'>
                    1
                </div>
            </div>
            <h3>
                <LinkTemplate path={`/tin-tuc/${data.path}`} className='news-card-title'>
                    {data.title}
                </LinkTemplate>
            </h3>
            <div style={{opacity: 0.7, marginBottom:'1em'}}>
                {data.summary}
            </div>
            <div style={{flexGrow: 1}}></div>
            <LinkTemplate path={`/tin-tuc/${data.path}`}>
                <span className='news-card-more'>ĐỌC TIẾP <i className="fas fa-caret-right"></i></span>
            </LinkTemplate>
        </div>
    );
};

export default NewsCard;